.rules_button_div {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
  width: 90vw;
}
.rules_button_div_a{
  align-self: flex-end;
}
.rules-button {
  border: none;
  border-radius: 2px;
  color: #223a5f;
  padding: 5px;
  align-self: flex-end;
}
.pop_container {
  width: 60vw;
  height: 60vh;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.pop_container_1 {
  align-self: flex-end;
}
.close-button {
  background-color: white;
  opacity: 0.5;
  border: none;
  align-items: center;
  padding: 2px;
}
.rules_image_div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  height: 90%;
}
.rules_img {
  width: 90%;
}
