* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.main_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  background-color: #223a5f;
  height: 100vh;
}
.result_div {
  border: solid 2px white;
  background-color: transparent;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 70%;
}
.names_div {
  display: flex;
  flex-direction: column;
  color: white;
  margin: 10px;
}
.main_heading {
  margin: 1px;

  font-size: 15px;
}
p {
  margin: 1px;
  font-weight: bold;
}

.score_div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  margin: 5px;
  padding: 5px;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 5px;
}
p {
  color: #223a5f;
  margin: 0px;
}
.score_p {
  font-weight: bold;
  font-size: 30px;
  font-family: Roboto;
}
.playing_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.playing_buttons_div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.playing_buttons_div_a {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.button_div {
  margin: 20px;
}
.rockButton {
  background-color: transparent;
  border: none;
}

img {
  width: 100px;
}

.result_view {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.result_view_1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.result_view_1_1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px;
}
.you {
  color: white;
}
.result {
  color: white;
  margin-top: 30px;
}
.playagain {
  margin: 5px;
}
.playagain_button {
  background-color: white;
  color: #223a5f;
  font-weight: bold;
  padding: 5px;
  width: 150px;
  text-align: center;
}
